import { html } from 'lit';

// –Ø–≤–Ω–æ –∏–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Å—Ç—Ä–∞–Ω–∏—Ü—ã –¥–ª—è —Å–±–æ—Ä—â–∏–∫–∞
import './pages/app-home.js';
import './pages/app-chat.js';

/**
 * –ò–Ω–∂–µ–Ω–µ—Ä–Ω–∞—è –∑–∞–≥–ª—É—à–∫–∞ –¥–ª—è —Å–æ–≤–º–µ—Å—Ç–∏–º–æ—Å—Ç–∏ —Å–æ —Å—Ç–∞—Ä—Ç–æ–≤—ã–º —à–∞–±–ª–æ–Ω–æ–º.
 * –ü—Ä–æ—Å—Ç–æ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –ø—É—Ç—å –∫–∞–∫ –µ—Å—Ç—å.
 */
export const resolveRouterPath = (path = '') => path;

export const router = {
  // –ù–∞—à —É–ø—Ä–æ—â–µ–Ω–Ω—ã–π —Ä–µ–Ω–¥–µ—Ä-–¥–≤–∏–∂–æ–∫
  render: () => {
    const path = window.location.pathname;
    
    // –ï—Å–ª–∏ –ø—Ä–æ–µ–∫—Ç –Ω–∞ GitHub Pages, –æ–Ω –º–æ–∂–µ—Ç –±—ã—Ç—å –≤ –ø–æ–¥–ø–∞–ø–∫–µ, 
    // –Ω–æ –¥–ª—è –Ω–∞—á–∞–ª–∞ –ø—Ä–æ–≤–µ—Ä—è–µ–º –∫–æ—Ä–µ–Ω—å
    if (path.includes('/chat')) {
      return html`<app-chat></app-chat>`;
    }
    
    return html`<app-home></app-home>`;
  },
  
  // –ú–µ—Ç–æ–¥ –¥–ª—è –ø—Ä–æ–≥—Ä–∞–º–º–Ω–æ–≥–æ –ø–µ—Ä–µ—Ö–æ–¥–∞
  goto: (path: string) => {
    window.history.pushState({}, '', path);
    window.dispatchEvent(new PopStateEvent('popstate'));
  }
};

// –°–ª—É—à–∞–µ–º –∏–∑–º–µ–Ω–µ–Ω–∏—è –∏—Å—Ç–æ—Ä–∏–∏ (–∫–Ω–æ–ø–∫–∞ "–ù–∞–∑–∞–¥")
window.addEventListener('popstate', () => {
  const appIndex = document.querySelector('app-index');
  if (appIndex) (appIndex as any).requestUpdate();
});
